<template>
  <div class="register-page">
    <TitleHeader title=""/>

    <form class="form" method="post" @submit.prevent="handleSubmit">
      <FormItem label="">
        <Input v-model="formData.user" placeholder="//" />
      </FormItem>
      <FormItem label="">
        <Input v-model="formData.password" type="password" placeholder="" />
      </FormItem>
      <FormItem label="">
        <Input v-model="formData.password2" type="password" placeholder="" />
      </FormItem>
      <Button class="submit" type="submit" :disabled="!isSubmit"></Button>
    </form>

    <Model v-model="visibleModel" cancelText="">
      <p class="modelMsg">{{ modelMsg }}</p>
    </Model>
  </div>
</template>

<script>
import FormItem    from '@/components/FormItem'
import Input       from '@/components/Input'
import Button      from '@/components/Button'
import Model       from '@/components/Model'
import TitleHeader from '@/components/TitleHeader'

export default {
  name: 'RegisterPage',
  data() {
    return {
      formData: {
        user: '',
        password: '',
        password2: ''
      },
      visibleModel: false,
      modelMsg: ''
    }
  },
  computed: {
    isSubmit () {
      return this.formData.user && this.formData.password && this.formData.password2
    }
  },
  components: {
    FormItem,
    Button,
    Input,
    Model,
    TitleHeader
  },
  methods: {
    handleSubmit () {
      if (this.formData.password !== this.formData.password2) {
        this.modelMsg = ''
        this.visibleModel = true
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.register-page {
  min-height: 100vh;
  background-color: #f0f0f0;
}

.form {
  display: flex;
  flex-direction: column;
  padding-top: 35px;
  font-size: 32px;
}

.submit {
  margin: 26px 20px 0 20px;
}

.modelMsg {
  padding: 20px 0;
  color: #6c6c6c;
  text-align: center;
}
</style>
