<template>
  <div class="login-page">
    <TitleHeader title=""/>

    <form class="form" method="post" @submit.prevent="handleSubmit">
      <FormItem label="">
        <Input v-model="formData.user" placeholder="//" />
      </FormItem>
      <FormItem v-show="!msgLogin" label="">
        <Input v-model="formData.password" type="password" placeholder="" />
      </FormItem>
      <FormItem v-show="msgLogin" label="" labelWidth="27%">
        <Input v-model="formData.msg" placeholder="" />
        <Button class="msg-button" theme="white" @click="getMsg"><span>{{ msgTime ? msgTime : '' }}</span></Button>
      </FormItem>
      <Button class="submit" type="submit" :disabled="!isSubmit"></Button>
    </form>

    <div class="other-link">
      <div class="link" @click="msgLogin = !msgLogin">{{ msgLogin ? '' : ''}}</div>
      <router-link :to="{ name: 'register' }" class="link"></router-link>
    </div>

    <Model v-model="visibleModel" cancelText="">
      <p class="modelMsg">{{ modelMsg }}</p>
    </Model>
  </div>
</template>

<script>
import Cookies     from 'js-cookie'
import FormItem    from '@/components/FormItem'
import Input       from '@/components/Input'
import Button      from '@/components/Button'
import Model       from '@/components/Model'
import TitleHeader from '@/components/TitleHeader'

export default {
  name: 'LoginPage',
  data() {
    return {
      formData: {
        user: '',
        password: '',
        msg: ''
      },
      visibleModel: false,
      msgLogin: false,
      msgTime: 0,
      modelMsg: ''
    }
  },
  computed: {
    isSubmit () {
      return this.formData.user && (this.msgLogin ? this.formData.msg : this.formData.password)
    }
  },
  components: {
    FormItem,
    Button,
    Input,
    Model,
    TitleHeader
  },
  methods: {
    handleSubmit () {
      // this.modelMsg = ''
      this.modelMsg = ''
      this.visibleModel = true

      Cookies.set('token', 'dasdfasfasfadfsafsadfsdfadgagds')

      setTimeout(() => {
        this.$router.replace('/')
      }, 500)
    },
    getMsg () {
      if (this.msgTime) {
        return
      }

      this.msgTime = 60
      const countdown = () => {
        this.msgTime --
        this.msgTime > 0 && setTimeout(countdown, 1000)
      }
      countdown()

      this.modelMsg = ''
      this.visibleModel = true
    }
  }
}
</script>

<style lang="scss" scoped>
.login-page {
  min-height: 100vh;
  background-color: #f0f0f0;
}

.form {
  display: flex;
  flex-direction: column;
  padding-top: 35px;
  font-size: 32px;
}

.msg-button {
  width: 280px;
  flex-shrink: 0;

  span {
    display: inline-block;
    border-left: 1px solid #c8c8c8;
    width: 100%;
  }
}

.submit {
  margin: 26px 20px 0 20px;
}

.other-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  height: 100px;
}

.link {
  display: flex;
  align-items: center;
  color: rgb(255, 0, 54);
  height: 80px;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.modelMsg {
  padding: 20px 0;
  color: #6c6c6c;
  text-align: center;
}
</style>

